* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
}

.backbut{
    position: absolute;
    z-index: 10;
}
.topsearchbar{
    position: relative;
    margin:3%;
  
    display: flex;
    flex-direction: row;
 
    align-items: center;
 
    
    height:40px;
    justify-content: space-around;
}


.userprofile{
  display: flex;
  flex-direction: row;
  animation-name: List_Search_animateIn__2ZQDQ__2-lAF;
  animation-duration: 350ms;
  animation-delay: calc(var(--animation-order) * 100ms);
  animation-fill-mode: both;
  animation-timing-function: ease-in-out;
  position: relative;
  border: 10px solid rgba(128, 128, 128, 0);
  border-radius: 15px;
  margin: 2%;
  align-items: center;
  box-shadow: 0 0 25px 0 rgb(191 193 230 / 84%), 0 20px 25px 0 rgb(0 0 0 / 20%);
  flex-wrap: wrap;
  align-content: space-between;
  justify-content: space-around;




  
}

@keyframes animateIn {
  0% {
    opacity: 0;
    transform: scale(0.6) translateY(-8px);
  }
  
  100% {
    opacity: 1;
  }
}
.userprofile img{
    width:20%;
    height:100%;
    border:2px solid rgba(255, 255, 255, 0);
    border-radius:5px;
    
}
.userprofile button{
  
    background:rgba(25, 250, 25, 0.479);
  width:50%;
    outline: none;
    border: 2px solid rgba(128, 128, 128, 0);
    border-radius: 100px;
  box-shadow: 2px 2px 5px rgba(28, 158, 49, 0.76);

    transition: ease-in-out;
    
}
.userprofile button:active{
  box-shadow: 2px 2px 10px rgba(118, 182, 128, 0.76);
}


.container {
  position: absolute;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 200px;
  height: 10px;
}
.container .search {
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 40px;
  height: 40px;
  background: rgb(20, 220, 110);
  border-radius: 50%;
  transition: all 1s;
  z-index: 4;
  box-shadow: 0 0 25px 0 rgba(0, 0, 0, 0.4);
}
.container .search:hover {
  cursor: pointer;
}
.container .search::before {
  content: "";
  position: absolute;
  margin: auto;
  top: 22px;
  right: 0;
  bottom: 0;
  left: 22px;
  width: 12px;
  height: 2px;
  background: white;
  transform: rotate(45deg);
  transition: all 0.5s;
}
.container .search::after {
  content: "";
  position: absolute;
  margin: auto;
  top: -5px;
  right: 0;
  bottom: 0;
  left: -5px;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 2px solid white;
  transition: all 0.5s;
}
.container input {

  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 50px;
  height: 50px;
  outline: none;
  border: none;
  background: rgb(20, 220, 70);
  color: white;
  text-shadow: 0 0 10px rgb(20, 220, 103);
  padding: 0 80px 0 20px;
  border-radius: 30px;
  box-shadow: 0 0 10px 0 rgb(20, 220, 47), 0 10px 15px 0 rgba(0, 0, 0, 0.2);
  transition: all 1s;
  opacity: 0;
  z-index: 5;
  font-weight: bolder;
  letter-spacing: 0.1em;
}
.container input:hover {
  cursor: pointer;
}
.container input:focus {
  width: 200px;
  opacity: 1;
  cursor: text;
}
.container input:focus ~ .search {
  right: -120px;
  background: #151515;
  z-index: 6;
}
.container input:focus ~ .search::before {
  top: 0;
  left: 0;
  width: 25px;
}
.container input:focus ~ .search::after {
  top: 0;
  left: 0;
  width: 25px;
  height: 2px;
  border: none;
  background: white;
  border-radius: 0%;
  transform: rotate(-45deg);
}
.container input::placeholder {
  color: white;
  opacity: 0.5;
  font-weight: bolder;
}
.loader,
.loader:after {
  border-radius: 50%;
  width: 10em;
  height: 10em;
}
.loader {
  margin: 60px auto;
  font-size: 10px;
   z-index: -19;
  position: sticky;
  text-indent: -9999em;
  border-top: 1.1em solid rgba(255, 255, 255, 0.2);
  border-right: 1.1em solid rgba(255, 255, 255, 0.2);
  border-bottom: 1.1em solid rgba(255, 255, 255, 0.2);
  border-left: 1.1em solid #c71e1e;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.searchbar{
  position: relative;
  width: 100vw;
  height: 7vh;

  display: flex;

}
.searchbar input{
  background-color: white;
  width: 80%;
  position: relative;
  height: 5vh;
  padding: 1em;
  border-radius: 10px;
  border: 0.2em solid #80808000;
  box-shadow: -0.04000000000000018em 0.4em 0.5em 0px grey;
  outline: none;
  animation: Search_boxAni__1z34p 1s ease-in-out infinite alternate-reverse;
}
.searchbar input:focus{
  animation: boxAni 100s ease-in-out infinite alternate-reverse ;
}
@keyframes boxAni {
  from{
    box-shadow: -0.04000000000000018em 0.4em 0.5em 0px grey;
  }to{
    box-shadow: -0.04000000000000018em 0.4em 2em 0px grey;
  }
}


.searchbtn{
  position: relative;
  margin-left: 10%;
  outline: none;
  border: 5px solid #80808030;
  box-shadow: 1px 1px 15px grey;
  background: blueviolet;
  border-radius: 20px;
  animation: Search_Search_boxAni__1z34p__2w5A8 1s ease-in-out alternate-reverse;
}
